"use client"

import { motion } from "framer-motion"

export default function ShimmerButton({ children, href }) {
  return (
    <motion.a
      href={href}
      whileHover={{ scale: 1.05 }}
      whileTap={{ scale: 0.97 }}
      className="relative inline-flex items-center justify-center px-6 py-3 font-semibold text-white rounded-xl overflow-hidden shimmer-btn"
    >
      {/* Glow aura */}
      <div className="absolute -inset-3 rounded-xl bg-indigo-500/30 blur-xl animate-spin-slow group-hover:opacity-90 transition-all duration-300 pointer-events-none z-[-1]"></div>

      {/* Shimmer light streak */}
      <div className="absolute -inset-3 rounded-xl bg-gradient-to-r from-blue-500 to-cyan-400 opacity-50 blur-xl animate-spin-slower group-hover:opacity-80 transition-all duration-300 pointer-events-none z-[-1]"></div>


      {/* Button Content */}
      <span className="relative z-10">{children}</span>
    </motion.a>
  )
}
